import React, { useState } from "react";
import ReactDOM from 'react-dom';
import './SearchPosts.css'
import Post from "../Post/Post";
import fetchDatas from "../../constants/functionfetch";
export default function SearchPosts() {
const [search,setSearch] = useState("");
const [type,setType] = useState("Posts");
const [postArray, setPostArray] = useState("")
const SearchText= async() => {
    let data =await fetchDatas(`/api/v1/search?text=${search}&tableName=${type}`);
   let [first,second] = data
    setPostArray(first);
    document.getElementsByClassName("posts")[0].style.display = "none"
    ReactDOM.render( <Posts favorite={"searched"} endpoint={""} first={first}/>,document.getElementById('searchedposts'));

}
  return (
    <div className="search" >
      <input placeholder="Search" name="name" onChange={(e) => setSearch(e.target.value)} />
      <button onClick={SearchText}>Search</button>
      {postArray ? (
            <div className="post-section">
              {postArray.map((item) => {
                return (
                  <Post
                    subtitle={item.subtitle}
                    img={item.image_url}
                    firstName={item.firstName}
                    createdAt={item.createdAt}
                    title={item.title}
                    description={item.description}
                    user_id={item.user_id}
                   
                  />
                );
              })}
            </div>
          ) : null}
    </div>
  );
};
